import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonContent,
  modalController,
  IonSegmentButton,
  IonSegment,
} from "@ionic/vue";
// import { Calendar } from 'v-calendar';
// import { CalendarModule } from "ion2-calendar";

import { Calendar } from "v-calendar";
import { defineComponent } from "vue";
import timepicker from "@/components/timepicker.vue";
import faker from "faker";
import { useRouter } from "vue-router";

// import {defineComponent} from 'vue'; and import defineComponent from 'vue'; the second one is not callable
export default defineComponent({
  name: "Tab2",
  components: {
    IonHeader,
    IonToolbar,
    IonTitle,
    IonContent,
    IonPage,
    Calendar,
    IonSegmentButton,
    IonSegment,
    // Calendar,
  },
  data() {
    return {
      config: {
        inline: true,
        position: "center",
        wrap: true,
        defaultDate: new Date(),
      },
      selectedDate: new Date(),
      attrs: [
        {
          dates: new Date(2021, 3, 30),
        },
        {
         
          dates: new Date(2021, 3, 20),
        },
        {
         
          dates: new Date(2021, 3, 15),
        },
        {
         
          dates: new Date(2021, 2, 25),
        },
        {
          highlight: "gray",
          dates: new Date(2021, 3, 3),
        },
        {
          highlight: "gray",
          dates: new Date(2021, 2, 6),
          disabled: true,
        },
      ],
    };
  },
  setup() {
    const router = useRouter();
    return { router };
  },
  methods: {
    segmentChanged() {
      this.attrs =[
        {
          highlight: "gray",
          dates: faker.date.soon(10),
        },
        {
          highlight: "gray",
          dates: faker.date.soon(10),
          disabled: true,
        },
      ];
    },
    async openModal() {
      const modal = await modalController.create({
        component: timepicker,
        backdropDismiss: true,
        swipeToClose: true,
        //@ts-ignore
        componentProps: { date: this.selectedDate.ariaLabel },
      });
      return modal.present();
    },

    onDayClick(day) {
      console.log(day);
      //
      this.selectedDate = day;
      this.openModal();
      // this.router.push("/picktime");
    },
    next() {
      console.log(this.selectedDate);
      this.openModal();
    },
    switchGrain(id) {
      //@ts-ignore

      for (let i = 0; i < 4; i++) {
        //@ts-ignore
        document
          .getElementById("tab2Button" + i)
          .setAttribute("enable", "false");
        // console.log(buttonId);
      } //
      const buttonId = "tab2Button" + id.toString();
      //@ts-ignore
      document.getElementById(buttonId).setAttribute("enable", "true");
      // document.getElementById(buttonId).className += " enabled";
      console.log(buttonId);
    },
  },
});
